// src/components/spatial/ContactOverlay.tsx
"use client";

import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { cn } from "@/lib/utils";

export function ContactOverlay() {
  const [submitted, setSubmitted] = useState(false);
  const [loading, setLoading] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    // Simulate a system handshake
    setTimeout(() => {
      setLoading(false);
      setSubmitted(true);
    }, 1500);
  };

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      className="fixed inset-0 z-[100] flex items-center justify-center bg-black/80 backdrop-blur-2xl"
    >
      {/* Glitch Effect Background Layer */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: [0, 0.2, 0.1, 0.3, 0] }}
        transition={{ repeat: Infinity, duration: 0.2 }}
        className="absolute inset-0 bg-[var(--grav-accent)] pointer-events-none mix-blend-overlay"
      />

      <motion.div
        initial={{ scale: 0.9, y: 20, opacity: 0 }}
        animate={{ scale: 1, y: 0, opacity: 1 }}
        transition={{ type: "spring", damping: 20, stiffness: 100 }}
        className="relative w-full max-w-md overflow-hidden border border-white/20 bg-zinc-900 p-1 shadow-2xl"
      >
        {/* Top scanline effect */}
        <div className="absolute top-0 left-0 w-full h-[1px] bg-white/20 animate-scanline" />

        <div className="bg-black p-8">
          <div className="flex items-center justify-between mb-8">
            <div className="space-y-1">
              <div className="text-[10px] font-mono uppercase tracking-[0.3em] text-orange-500">
                Match_Found
              </div>
              <h2 className="text-xl font-bold tracking-tight text-white">
                Direct Collaboration
              </h2>
            </div>
            <div className="text-right font-mono text-[9px] text-zinc-500">
              REF_ID: {Math.random().toString(36).substring(7).toUpperCase()}
            </div>
          </div>

          {!submitted ? (
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="group relative">
                <label className="text-[9px] font-mono uppercase tracking-widest text-zinc-500 transition-colors group-focus-within:text-[var(--grav-accent)]">
                  Project_Identity
                </label>
                <input
                  required
                  type="text"
                  placeholder="What are we naming this?"
                  className="w-full bg-transparent border-b border-white/10 py-3 text-sm outline-none transition-colors focus:border-white/40 placeholder:text-zinc-700"
                />
              </div>

              <div className="group relative">
                <label className="text-[9px] font-mono uppercase tracking-widest text-zinc-500 transition-colors group-focus-within:text-[var(--grav-accent)]">
                  Psychological_Goal
                </label>
                <textarea
                  required
                  placeholder="What should people feel when they use this?"
                  className="w-full h-24 bg-transparent border-b border-white/10 py-3 text-sm outline-none transition-colors focus:border-white/40 placeholder:text-zinc-700 resize-none"
                />
              </div>

              <div className="group relative">
                <label className="text-[9px] font-mono uppercase tracking-widest text-zinc-500 transition-colors group-focus-within:text-[var(--grav-accent)]">
                  Return_Path
                </label>
                <input
                  required
                  type="email"
                  placeholder="Your_Email_Address"
                  className="w-full bg-transparent border-b border-white/10 py-3 text-sm outline-none transition-colors focus:border-white/40 placeholder:text-zinc-700"
                />
              </div>

              <button
                disabled={loading}
                type="submit"
                className={cn(
                  "relative w-full py-4 mt-4 font-mono text-[10px] font-bold tracking-[0.4em] uppercase transition-all overflow-hidden",
                  "bg-white text-black hover:bg-[var(--grav-accent)] hover:text-white disabled:opacity-50"
                )}
              >
                {loading ? "Transmitting..." : "Send_Signal"}
              </button>
            </form>
          ) : (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              className="py-12 text-center"
            >
              <div className="mb-4 text-orange-500">●</div>
              <h3 className="text-lg font-bold mb-2">Signal Received.</h3>
              <p className="text-xs text-zinc-500 leading-relaxed font-mono uppercase tracking-widest">
                The connection is established. <br /> I will reach out shortly.
              </p>
            </motion.div>
          )}
        </div>
      </motion.div>
    </motion.div>
  );
}